import React, { useState, useEffect, useMemo } from "react";
import { Typography, Box, Fab } from "@mui/material";

const CountdownTimer = ({
  targetDateTime,
  examTimeUp,
  testDurationInSeconds,
  disableAbsolutePosition,
}) => {
  const [timeLeft, setTimeLeft] = useState(calculateTimeLeft());

  function calculateTimeLeft() {
    const targetTime = new Date(targetDateTime).getTime();
    const now = new Date().getTime();
    return Math.max(0, Math.floor((targetTime - now) / 1000)); // Convert to seconds
  }

  useEffect(() => {
    const interval = setInterval(() => {
      setTimeLeft(calculateTimeLeft());
    }, 1000);

    return () => clearInterval(interval);
  }, [targetDateTime]);

  useEffect(() => {
    if (timeLeft <= 0) {
      examTimeUp();
    } else if (timeLeft <= 60) {
      setIsBlinking(true);
    } else {
      setIsBlinking(false);
    }
  }, [timeLeft]);

  const [isBlinking, setIsBlinking] = useState(timeLeft <= 60);

  const formatTime = (time) => {
    const hours = Math.floor(time / 3600);
    const minutes = Math.floor((time % 3600) / 60);
    const seconds = time % 60;
    return { hours, minutes, seconds };
  };

  const { hours, minutes, seconds } = formatTime(timeLeft);
  const fabColor = useMemo(() => {
    if (timeLeft <= 60) {
      return "error";
    }

    const percentLeft = Math.round((timeLeft / testDurationInSeconds) * 100);

    if (percentLeft <= 20) {
      return "warning";
    }

    return "success";
  }, [timeLeft, testDurationInSeconds]);

  return (
    <Fab
      variant="extended"
      sx={
        !disableAbsolutePosition && {
          position: "fixed",
          top: 10,
        }
      }
      color={fabColor}
      disableRipple
    >
      <Box
        sx={{
          animation: isBlinking ? "blink 1s infinite alternate" : "none",
          "@keyframes blink": {
            "0%": { opacity: 1 },
            "100%": { opacity: 0.3 },
          },
        }}
        display="flex"
        flexDirection="row"
        alignItems="center"
        gap={1}
      >
        <Typography variant="h6">Time:</Typography>
        <Typography variant="h4">
          {`${String(hours).padStart(2, "0")}:${String(minutes).padStart(
            2,
            "0"
          )}:${String(seconds).padStart(2, "0")}`}
        </Typography>
      </Box>
    </Fab>
  );
};

export default CountdownTimer;
