import React, { useState } from "react";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from "@mui/material";

const FinishExam = ({ onFinish, isFinishing }) => {
  const [open, setOpen] = useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleConfirmFinish = async () => {
    if (await onFinish()) {
      setOpen(false);
    }
  };

  return (
    <>
      <Button variant="contained" color="error" onClick={handleClickOpen}>
        Finish Exam
      </Button>

      {/* Confirmation Dialog */}
      <Dialog open={open} onClose={handleClose}>
        <DialogTitle>Confirm Finish</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Are you sure you want to finish the exam? Once you submit, you wont
            be able to make changes.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button
            onClick={handleConfirmFinish}
            color="error"
            variant="contained"
            loading={isFinishing}
          >
            Finish Exam
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
};

export default FinishExam;
