import React, { useEffect, useState } from "react";
import { makeStyles } from "@mui/styles";
import {
  Container,
  Typography,
  Paper,
  Grid,
  Radio,
  RadioGroup,
  FormControlLabel,
  Button,
  AppBar,
  Box,
  Divider,
} from "@mui/material";
import SchoolLogo from "../../assets/logo.png";
import convertIndexToAlphabet from "../../utils/convertIndexToAlphabet";
import classnames from "classnames";

const useStyles = makeStyles((theme) => ({
  root: {
    marginTop: 64,
  },
  paper: {
    padding: 16,
  },
  question: {
    marginBottom: 16,
  },
  submitButton: {
    marginTop: 16,
  },
  appBar: {
    marginBottom: 42,
    paddingRight: 15,
    paddingLeft: 15,
  },
  toolbar: {
    display: "flex",
    justifyContent: "space-between",
  },
  avatar: {
    height: 48,
  },
  defaultLabel: {
    backgroundColor: "#DDDDDD",
    paddingTop: 1,
    paddingBottom: 1,
    paddingLeft: 5,
    paddingRight: 5,
    borderRadius: 2,
    gap: 3,
  },
  radio: {
    padding: "0px !important",
  },
  redLabel: {
    backgroundColor: "#FFBBBB",
  },
  greenLabel: {
    backgroundColor: "#BBFFBB",
  },
}));

export const convertNumberToBoolean = (value) => {
  if (value === "1") {
    return true;
  }
  if (value === "0") {
    return false;
  }
  return null;
};

export const convertBooleanToNumber = (value) => {
  if (value === true) {
    return 1;
  }
  if (value === false) {
    return 0;
  }
  return -1;
};

const ExamMultipleOptions = ({
  options,
  question,
  examId,
  onOptionSelected,
}) => {
  const classes = useStyles();

  const possibleMultiAnswers = [
    { name: "Not Attempted", value: -1 },
    { name: "False", value: 0 },
    { name: "True", value: 1 },
  ];

  const handeOptionChange = (event) => {
    const { value, name: optionId } = event.target;

    onOptionSelected(question.id, optionId, convertNumberToBoolean(value));
  };

  return options.map((option, optionIndex) => {
    return (
      <Box
        key={`${optionIndex}`}
        display="flex"
        flexDirection="row"
        alignItems="center"
        gap={3}
      >
        <Typography variant="body1" fontWeight="bold">
          {convertIndexToAlphabet(optionIndex)}.
        </Typography>
        <Box display="flex" flexDirection="row">
          <Box display="flex" flexDirection="row" flexWrap="nowrap">
            <RadioGroup
              name={option.id}
              size="small"
              defaultValue={-1}
              row
              value={convertBooleanToNumber(option.selected)}
              onChange={handeOptionChange}
            >
              {possibleMultiAnswers.map((possibleAnswer) => (
                <FormControlLabel
                  key={possibleAnswer.name}
                  value={possibleAnswer.value}
                  control={<Radio size="small" className={classes.radio} />}
                  label={possibleAnswer.name}
                  className={classnames(classes.defaultLabel, {
                    [classes.redLabel]: possibleAnswer.name === "False",
                    [classes.greenLabel]: possibleAnswer.name === "True",
                  })}
                  slotProps={{
                    typography: {
                      variant: "body2",
                    },
                  }}
                />
              ))}
            </RadioGroup>
          </Box>
          <Typography variant="body1" ml={-0.5}>
            {option.answerText}
          </Typography>
        </Box>
      </Box>
    );
  });
};

export default ExamMultipleOptions;
