import React, { Children, useEffect, useState } from "react";
import { makeStyles } from "@mui/styles";
import {
  Container,
  Typography,
  Paper,
  Grid,
  Radio,
  RadioGroup,
  FormControlLabel,
  Button,
  AppBar,
  Box,
  Divider,
  ButtonGroup,
  Pagination,
  PaginationItem,
  CircularProgress,
} from "@mui/material";
import SchoolLogo from "../../assets/logo.png";
import ExamQuestionCard from "../../components/ExamQuestionCard/ExamQuestionCard";
import LoadingButton from "@mui/lab/LoadingButton";
import ChevronLeftIcon from "@mui/icons-material/ChevronLeft";
import ChevronRightIcon from "@mui/icons-material/ChevronRight";
import ExamQuotes from "../../components/ExamQuotes/ExamQuotes";
import useExams from "../../hooks/useExams";
import { useParams } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  root: {
    marginTop: 64,
  },
  paper: {
    padding: 16,
  },
  question: {
    marginBottom: 16,
  },
  submitButton: {
    marginTop: 16,
  },
  appBar: {
    marginBottom: 42,
    paddingRight: 15,
    paddingLeft: 15,
  },
  toolbar: {
    display: "flex",
    justifyContent: "space-between",
  },
  avatar: {
    height: 48,
  },
  pagination: {
    "& ul": {
      gap: 7,
    },
  },
  loaderContainer: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    height: "100vh",
    width: "100vw",
  },
  loader: {
    width: "100px !important",
    height: "100px !important",
  },
}));

const StartExam = ({ children, startExam, examState, exams }) => {
  const classes = useStyles();
  const routeParams = useParams();
  const { examId } = routeParams;
  const { isStartingExam, loadedExamId } = examState;

  useEffect(() => {
    (async () => {
      if (examId && exams.length > 0) {
        await startExam(examId);
      }
    })();
  }, [examId, exams]);

  return isStartingExam || loadedExamId == null ? (
    <Box pt={40} display="flex" flexDirection="column" gap={2}>
      <Box>
        <svg width={0} height={0}>
          <defs>
            <linearGradient id="my_gradient" x1="0%" y1="0%" x2="0%" y2="100%">
              <stop offset="0%" stopColor="#e01cd5" />
              <stop offset="100%" stopColor="#1CB5E0" />
            </linearGradient>
          </defs>
        </svg>
        <CircularProgress
          size={100}
          thickness={2}
          sx={{ "svg circle": { stroke: "url(#my_gradient)" } }}
        />
      </Box>
      <Typography variant="subtitle2">
        Please wait while we prepare your exams...
      </Typography>
      <ExamQuotes />
    </Box>
  ) : (
    children
  );
};

export default StartExam;
