import authenticationService from "./authentication";

const httpService = {};

const apiEndpoint = process.env.REACT_APP_EXAMS_API_URL;

export class BackendError extends Error {
  constructor(status, body) {
    super(body.message);
    this.name = body.name;
    this.status = status;
  }
}

async function request(route, method, data, headers, ignore401Error = false) {
  try {
    const { accessToken } = authenticationService.getUserAndToken();

    const response = await fetch(`${apiEndpoint}/${route}`, {
      method,
      headers: {
        "Content-Type": "application/json",
        ...(accessToken && { Authorization: `Bearer ${accessToken}` }),
        ...headers,
      },
      ...(data && { body: JSON.stringify(data) }),
    });

    const responseText = await response.text();

    let responseJson;

    if (responseText.length > 0) {
      responseJson = JSON.parse(responseText);
    }

    if (response.ok) {
      return responseJson;
    }

    throw new BackendError(response.status, responseJson);
  } catch (error) {
    if (!ignore401Error && error.status == 401) {
      await authenticationService.destroyUserSession();
      window.location.href = "/";
    }

    throw error;
  }
}

httpService.post = async (
  route,
  data,
  headers = {},
  ignore401Error = false
) => {
  return request(route, "POST", data, headers, ignore401Error);
};

httpService.get = async (route, headers = {}) => {
  return request(route, "GET", null, headers);
};

export default httpService;
