import httpService from "./http";
import * as queueStorage from "./queueStorage";

const examService = {};

examService.fetchUserExams = async () => {
  try {
    const availableExams = await httpService.get("exams");

    return availableExams;
  } catch (error) {
    // throw error;

    await new Promise((resolve) => {
      resolve();
    }, 3000);

    return await examService.fetchUserExams();
  }
};

examService.startUserExam = async (examId) => {
  const examQuestions = await httpService.post(`exams/${examId}/start`);

  return examQuestions;
};

examService.userExamTimeUp = async (examId) => {
  await queueStorage.addRequest(
    `exams/${examId}/time-up`,
    "post",
    {},
    "userExamTimeUp",
    { examId }
  );
};

examService.terminateUserExam = async (examId) => {
  await queueStorage.addRequest(
    `exams/${examId}/terminate`,
    "post",
    {},
    "terminateUserExam",
    { examId }
  );
};

examService.markQuestionAsSeen = async (examId, questionId) => {
  await queueStorage.addRequest(
    `exams/${examId}/questions/${questionId}/mark-as-seen`,
    "post",
    {},
    "markQuestionAsSeen",
    { examId, questionId }
  );
};

examService.submitAnAnswer = async (
  examId,
  questionId,
  optionId,
  testLogId,
  ansTestLogId,
  reactionTime,
  answer,
  answerText
) => {
  await queueStorage.addRequest(
    `exams/${examId}/questions/${questionId}/answer`,
    "post",
    {
      optionId,
      testLogId,
      ansTestLogId,
      reactionTime,
      answer,
      answerText,
      testChangeTime: new Date(),
    },
    "submitAnAnswer",
    {
      examId,
      questionId,
      optionId,
      testLogId,
      ansTestLogId,
      reactionTime,
      answer,
      answerText,
      testChangeTime: new Date(),
    }
  );
};

export default examService;
