import React, { useEffect, useState, createContext, useMemo } from "react";
import { makeStyles } from "@mui/styles";
import {
  Container,
  Typography,
  Paper,
  Grid,
  Radio,
  RadioGroup,
  FormControlLabel,
  Button,
  AppBar,
  Box,
  Divider,
  ButtonGroup,
  Pagination,
  PaginationItem,
  CircularProgress,
} from "@mui/material";
import SchoolLogo from "../../assets/logo.png";
import ExamQuestionCard from "../../components/ExamQuestionCard/ExamQuestionCard";
import LoadingButton from "@mui/lab/LoadingButton";
import ChevronLeftIcon from "@mui/icons-material/ChevronLeft";
import ChevronRightIcon from "@mui/icons-material/ChevronRight";
import ExamQuotes from "../../components/ExamQuotes/ExamQuotes";
import StartExam from "./StartExam";
import useExams from "../../hooks/useExams";
import { useNavigate, useParams } from "react-router-dom";
import CountdownTimer from "./CountdownTimer";
import FinishExam from "./FinishExam";
import ExamTimeoutCard from "./ExamTimeoutCard";
import ExamCompletedCard from "./ExamCompletedCard";

const useStyles = makeStyles((theme) => ({
  root: {
    marginTop: 64,
  },
  paper: {
    padding: 16,
  },
  question: {
    marginBottom: 16,
  },
  submitButton: {
    marginTop: 16,
  },
  appBar: {
    marginBottom: 42,
    paddingRight: 15,
    paddingLeft: 15,
  },
  toolbar: {
    display: "flex",
    justifyContent: "space-between",
  },
  avatar: {
    height: 48,
  },
  pagination: {
    "& ul": {
      gap: 7,
    },
  },
  loaderContainer: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    height: "100vh",
    width: "100vw",
  },
  loader: {
    width: "100px !important",
    height: "100px !important",
  },
}));

const StudentExam = () => {
  const classes = useStyles();
  const {
    examState,
    exams,
    startExam,
    setCurrentQuestionIndex,
    hasSeenAndAnsweredQuestion,
    hasSeenQuestion,
    onOptionSelected,
    examTimeUp,
    finishExam,
    onAnswerWritten,
  } = useExams();
  const routeParams = useParams();
  const { examId } = routeParams;
  const {
    questions,
    currentQuestionIndex,
    testEndTime,
    timeUp,
    testDurationInSeconds,
    isFinished,
    isFinishing,
    examError,
    isNotLoaded,
    message: errorMessage,
  } = examState;

  const handleSubmit = () => {
    // Here you can handle the submission of answers
    // You can send the answers to a backend for evaluation or processing
  };

  const [timeLeft, setTimeLeft] = useState(60 * 60); // 1 hour in seconds

  useEffect(() => {
    const timer = setTimeout(() => {
      setTimeLeft(timeLeft - 1);
    }, 1000);

    return () => clearTimeout(timer);
  }, [timeLeft]);

  const handlePreviousPage = () => {
    if (currentQuestionIndex - 1 >= 0) {
      setCurrentQuestionIndex(currentQuestionIndex - 1);
    }
  };

  const handleNextPage = () => {
    if (currentQuestionIndex + 1 < questions.length) {
      setCurrentQuestionIndex(currentQuestionIndex + 1);
    }
  };

  const handleSelectPage = (page) => {
    if (page - 1 >= 0) {
      setCurrentQuestionIndex(page - 1);
    }
  };

  const ExamStatusCard = useMemo(() => {
    if (isNotLoaded) return ExamTimeoutCard;
    if (isFinished) return ExamCompletedCard;

    return ExamTimeoutCard;
  }, [isFinished, isNotLoaded]);

  const currentlySelectedQuestion = questions[currentQuestionIndex];

  const examDetails = exams.find(({ id }) => id == examId) || {};

  return (
    <StartExam startExam={startExam} examState={examState} exams={exams}>
      {!timeUp && !isFinished && !examError ? (
        <Box>
          <Box display="flex" flexDirection="column" gap={2} my={5}>
            <Typography variant="subtitle2" gutterBottom>
              Exam in Progress:
            </Typography>
            <Typography variant="h4" gutterBottom>
              {examDetails.title}
            </Typography>
          </Box>

          <Paper className={classes.paper} elevation={1}>
            <Grid container spacing={3}>
              <ExamQuestionCard
                examId={examId}
                question={currentlySelectedQuestion}
                onOptionSelected={onOptionSelected}
                onAnswerWritten={onAnswerWritten}
              />
            </Grid>
          </Paper>

          <Box display="flex">
            <ButtonGroup
              variant="contained"
              aria-label="Loading button group"
              size="large"
              spacing={2}
            >
              <LoadingButton
                loadingPosition="start"
                startIcon={<ChevronLeftIcon />}
                onClick={handlePreviousPage}
                disabled={currentQuestionIndex <= 0}
              >
                Previous
              </LoadingButton>
              <LoadingButton
                loadingPosition="start"
                endIcon={<ChevronRightIcon />}
                onClick={handleNextPage}
                disabled={currentQuestionIndex + 1 >= questions.length}
              >
                Next
              </LoadingButton>
            </ButtonGroup>
          </Box>

          <Box mt={10}>
            <Paper sx={{ paddingY: 1, paddingX: 1 }}>
              <Pagination
                count={questions.length}
                renderItem={(item) => {
                  return (
                    <PaginationItem
                      {...item}
                      color={
                        currentQuestionIndex == item.page - 1
                          ? "secondary"
                          : hasSeenAndAnsweredQuestion(item.page - 1)
                          ? "success"
                          : hasSeenQuestion(item.page - 1)
                          ? "primary"
                          : "standard"
                      }
                      selected={
                        hasSeenAndAnsweredQuestion(item.page - 1) ||
                        hasSeenQuestion(item.page - 1) ||
                        currentQuestionIndex == item.page - 1
                      }
                    />
                  );
                }}
                hideNextButton
                hidePrevButton
                size="medium"
                boundaryCount={questions.length}
                shape="rounded"
                variant="outlined"
                className={classes.pagination}
                page={currentQuestionIndex + 1}
                onChange={(event, page) => handleSelectPage(page)}
              />
            </Paper>
          </Box>

          <Box mt={5}>
            <FinishExam onFinish={finishExam} isFinishing={isFinishing} />
          </Box>

          {questions && (
            <CountdownTimer
              targetDateTime={testEndTime}
              examTimeUp={examTimeUp}
              testDurationInSeconds={testDurationInSeconds}
            />
          )}
        </Box>
      ) : (
        <Box>
          <Box display="flex" flexDirection="column" gap={2} my={5}>
            <ExamStatusCard
              examTitle={examDetails.test_name}
              examError={examError}
              message={errorMessage}
            >
              {!isFinished && questions && (
                <CountdownTimer
                  targetDateTime={testEndTime}
                  examTimeUp={examTimeUp}
                  testDurationInSeconds={testDurationInSeconds}
                  disableAbsolutePosition
                />
              )}
            </ExamStatusCard>
          </Box>
        </Box>
      )}
    </StartExam>
  );
};

export default StudentExam;
