import React, { useState } from "react";
import { makeStyles } from "@mui/styles";
import {
  Container,
  Paper,
  Typography,
  TextField,
  Button,
  Box,
  Alert,
} from "@mui/material";
import backgroundImage from "../../assets/home-bg.jpg";
import Zoom from "@mui/material/Zoom";

import Logo from "../../assets/logo.png";
import { useNavigate } from "react-router-dom";
import authenticationService from "../../services/authentication";

const useStyles = makeStyles(() => ({
  root: {
    height: "100vh",
    display: "flex !important",
    justifyContent: "center",
    alignItems: "center",
    flexDirection: "column",
    alignContent: "center",
    gap: 150,
  },

  paper: {
    padding: 24,
    width: 300,
    marginTop: -20,
    paddingBottom: 45,
  },
  form: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    gap: 5,
  },
  textField: {
    marginBottom: 16,
  },
  loginButton: {
    marginTop: 16,
  },
  logo: {
    width: "100%",
    marginTop: 16,
  },
  container: {
    backgroundImage: `url(${backgroundImage})`,
    backgroundSize: "cover",
    backgroundPosition: "center",
    backgroundRepeat: "no-repeat",
  },
}));

const Login = () => {
  const classes = useStyles();
  const [inputs, setInputs] = useState({
    username: "",
    password: "",
    error: "",
    loading: false,
  });

  const navigate = useNavigate();

  const handleSubmit = async (event) => {
    event.preventDefault();
    setInputs((prevState) => ({ ...prevState, loading: true, error: "" }));

    try {
      await authenticationService.loginUser(inputs.username, inputs.password);

      navigate("/courses");
    } catch (error) {
      console.log(error);
      setInputs((prevState) => ({
        ...prevState,
        loading: true,
        error: error.message,
      }));
    }
  };

  const handleInputChange = (event) => {
    const { value, name } = event.target;

    setInputs((prevState) => ({ ...prevState, [name]: value }));
  };

  return (
    <Box className={classes.container}>
      <Zoom appear in timeout={500}>
        <Container component="main" maxWidth="xs" className={classes.root}>
          <Paper elevation={3} className={classes.paper}>
            <Box display="flex" flexDirection="column" gap={5}>
              <Box>
                <img src={Logo} alt="Logo" className={classes.logo} />
              </Box>
              <Box display="flex" flexDirection="column" gap={6}>
                <Typography component="h1" variant="h5">
                  CBT Exam Login
                </Typography>
                <form className={classes.form} onSubmit={handleSubmit}>
                  {inputs.error && (
                    <Alert variant="filled" severity="warning">
                      {inputs.error}
                    </Alert>
                  )}
                  <TextField
                    variant="outlined"
                    margin="normal"
                    required
                    fullWidth
                    id="username"
                    label="Username / Matric Number"
                    name="username"
                    autoComplete="username"
                    autoFocus
                    value={inputs.username}
                    onChange={handleInputChange}
                    className={classes.textField}
                  />
                  <TextField
                    variant="outlined"
                    margin="normal"
                    required
                    fullWidth
                    name="password"
                    label="Password"
                    type="password"
                    value={inputs.password}
                    onChange={handleInputChange}
                    className={classes.textField}
                  />
                  <Button
                    type="submit"
                    fullWidth
                    variant="contained"
                    color="primary"
                    className={classes.loginButton}
                    disabled={!(inputs.username && inputs.password)}
                  >
                    Log In
                  </Button>
                </form>
              </Box>
            </Box>
          </Paper>
        </Container>
      </Zoom>
    </Box>
  );
};

export default Login;
