import React, { useEffect, useState } from "react";
import { makeStyles } from "@mui/styles";
import {
  Container,
  Typography,
  Paper,
  Grid,
  Radio,
  RadioGroup,
  FormControlLabel,
  Button,
  AppBar,
  Box,
  Divider,
} from "@mui/material";
import SchoolLogo from "../../assets/logo.png";
import convertIndexToAlphabet from "../../utils/convertIndexToAlphabet";
import classnames from "classnames";
import {
  convertBooleanToNumber,
  convertNumberToBoolean,
} from "../ExamMultipleOptions/ExamMultipleOptions";

const useStyles = makeStyles((theme) => ({
  root: {
    marginTop: 64,
  },
  paper: {
    padding: 16,
  },
  question: {
    marginBottom: 16,
  },
  submitButton: {
    marginTop: 16,
  },
  appBar: {
    marginBottom: 42,
    paddingRight: 15,
    paddingLeft: 15,
  },
  toolbar: {
    display: "flex",
    justifyContent: "space-between",
  },
  avatar: {
    height: 48,
  },
  defaultLabel: {
    backgroundColor: "#DDDDDD",
    paddingTop: 1,
    paddingBottom: 1,
    paddingLeft: 5,
    paddingRight: 5,
    borderRadius: 2,
    gap: 3,
  },
  radio: {
    padding: "0px !important",
  },
  redLabel: {
    backgroundColor: "#FFBBBB",
  },
  greenLabel: {
    backgroundColor: "#BBFFBB",
  },
}));

const ExamSingleOption = ({ options, question, examId, onOptionSelected }) => {
  const classes = useStyles();

  const handeOptionChange = (event) => {
    const { value, name: optionId } = event.target;

    onOptionSelected(
      question.id,
      optionId,
      convertNumberToBoolean("1"),
      convertNumberToBoolean("0")
    );
  };

  return options.map((option, optionIndex) => {
    return (
      <Box
        key={`${optionIndex}`}
        display="flex"
        flexDirection="row"
        alignItems="center"
        gap={3}
      >
        <Typography variant="body1" fontWeight="bold">
          {convertIndexToAlphabet(optionIndex)}.
        </Typography>
        <Box display="flex" flexDirection="row">
          <RadioGroup
            name={option.id}
            size="small"
            defaultValue={-1}
            row
            value={convertBooleanToNumber(option.selected)}
            onChange={handeOptionChange}
          >
            <FormControlLabel
              key={option.name}
              value={option.id}
              control={
                <Radio
                  size="small"
                  className={classnames(classes.radio, {
                    [classes.greenLabel]: option.selected,
                  })}
                />
              }
              label={
                <Typography variant="body1" ml={1.5}>
                  {option.answerText}
                </Typography>
              }
              slotProps={{
                typography: {
                  variant: "body2",
                },
              }}
              checked={option.selected > 0}
            />
          </RadioGroup>
        </Box>
      </Box>
    );
  });
};

export default ExamSingleOption;
