import React from "react";
import {
  Paper,
  Typography,
  Button,
  Box,
  CircularProgress,
} from "@mui/material";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import { useNavigate } from "react-router-dom";
import { useQueueLength } from "../../services/queueProcessorService"; // Import useQueueLength

const ExamCompletedCard = () => {
  const navigate = useNavigate();
  const queueLength = useQueueLength(); // Get the current queue length

  const onExit = () => {
    navigate("/courses");
  };

  return (
    <Paper
      elevation={6}
      sx={{
        padding: 3,
        textAlign: "center",
        maxWidth: 400,
        margin: "auto",
        backgroundColor: "#e8f5e9",
        border: "1px solid #4caf50",
        borderRadius: 2,
      }}
    >
      <Box display="flex" justifyContent="center" mb={2}>
        <CheckCircleIcon sx={{ fontSize: 60, color: "#388e3c" }} />
      </Box>
      <Typography variant="h5" fontWeight="bold" color="#388e3c">
        Exam Completed!
      </Typography>
      <Typography variant="body1" color="textSecondary" sx={{ mt: 1, mb: 3 }}>
        Congratulations! You have successfully completed your exam. Your results
        would be announced later by the instructor.
      </Typography>
      {queueLength > 0 && (
        <Box
          display="flex"
          alignItems="center"
          justifyContent="center"
          sx={{ mt: -2, mb: 2 }}
        >
          <CircularProgress size={20} sx={{ mr: 1, color: "#388e3c" }} />
          <Typography variant="body2" color="#388e3c" fontWeight="medium">
            Still submitting your answers, please wait... ({queueLength}{" "}
            pending)
          </Typography>
        </Box>
      )}
      <Box display="flex" justifyContent="center" gap={2}>
        <Button
          variant="outlined"
          color="secondary"
          onClick={onExit}
          disabled={queueLength > 0} // Disable exit button if queue is not empty
        >
          Exit
        </Button>
      </Box>
    </Paper>
  );
};

export default ExamCompletedCard;
