import localForage from "localforage";

const requestStore = localForage.createInstance({
  name: "ExamAppDB",
  storeName: "queuedRequests",
  description: "Stores exam requests for reliable offline processing",
});

export const addRequest = async (
  url,
  httpMethod,
  body,
  originalMethodName,
  originalArgs
) => {
  const requestId = `req_${Date.now()}_${Math.random()
    .toString(36)
    .slice(2, 11)}`;
  const requestData = {
    url,
    httpMethod,
    body,
    originalMethodName,
    originalArgs,
    timestamp: Date.now(),
  };

  try {
    await requestStore.setItem(requestId, requestData);
    console.log(
      `[QueueStorage] Request '${originalMethodName}' (${httpMethod} ${url}) added with ID: ${requestId}`
    );
    return requestId;
  } catch (error) {
    console.error(
      `[QueueStorage] Error adding request '${originalMethodName}' to queue:`,
      error
    );
    throw error;
  }
};

export const getOldestRequest = async () => {
  let oldestRequest = null;
  let oldestId = null;

  try {
    await requestStore.iterate((value, key) => {
      if (!oldestRequest || value.timestamp < oldestRequest.timestamp) {
        oldestRequest = value;
        oldestId = key;
      }
    });
    return oldestId ? { id: oldestId, request: oldestRequest } : null;
  } catch (error) {
    console.error("[QueueStorage] Error retrieving oldest request:", error);
    throw error;
  }
};

export const removeRequest = async (requestId) => {
  try {
    await requestStore.removeItem(requestId);
    console.log(`[QueueStorage] Request '${requestId}' removed.`);
  } catch (error) {
    console.error(
      `[QueueStorage] Error removing request '${requestId}':`,
      error
    );
    throw error;
  }
};

export const getQueueLength = async () => {
  try {
    const length = await requestStore.length();
    return length;
  } catch (error) {
    console.error("[QueueStorage] Error getting queue length:", error);
    throw error;
  }
};

export const clearQueue = async () => {
  try {
    await requestStore.clear();
    console.warn("[QueueStorage] All queued requests cleared!");
  } catch (error) {
    console.error("[QueueStorage] Error clearing queue:", error);
    throw error;
  }
};
