import React from "react";
import {
  Paper,
  Typography,
  Button,
  Box,
  CircularProgress,
} from "@mui/material";
import WarningAmberIcon from "@mui/icons-material/WarningAmber";
import { useNavigate } from "react-router-dom";
import { useQueueLength } from "../../services/queueProcessorService";

const ExamTimeoutCard = ({ examTitle, children, message }) => {
  const navigate = useNavigate();
  const queueLength = useQueueLength();

  const onExit = () => {
    navigate("/courses");
  };

  return (
    <Paper
      elevation={6}
      sx={{
        padding: 3,
        textAlign: "center",
        maxWidth: 400,
        margin: "auto",
        backgroundColor: "#ffebee",
        border: "1px solid #f44336",
        borderRadius: 2,
      }}
    >
      <Box display="flex" flexDirection="column" gap={3}>
        <Box display="flex" justifyContent="center" mb={2}>
          <WarningAmberIcon sx={{ fontSize: 60, color: "#d32f2f" }} />
        </Box>
        <Typography variant="h6" fontWeight="bold" color="#d32f2f">
          {examTitle}
        </Typography>
        <Typography variant="h5" fontWeight="bold" color="#d32f2f">
          Exam Timed Out!
        </Typography>
        <Typography variant="body1" color="textSecondary" sx={{ mt: 1, mb: 3 }}>
          {message || `Your exam session has expired due to time running out.`}{" "}
          Please contact your instructor if you need assistance.
        </Typography>
        {children}
        {queueLength > 0 && (
          <Box
            display="flex"
            alignItems="center"
            justifyContent="center"
            sx={{ mt: -2, mb: 2 }}
          >
            <CircularProgress size={20} sx={{ mr: 1, color: "#d32f2f" }} />
            <Typography variant="body2" color="#d32f2f" fontWeight="medium">
              Still submitting your answers, please wait... ({queueLength}{" "}
              pending)
            </Typography>
          </Box>
        )}
        <Box display="flex" justifyContent="center" gap={2}>
          <Button
            variant="outlined"
            color="secondary"
            onClick={onExit}
            disabled={queueLength > 0}
          >
            Exit
          </Button>
        </Box>
      </Box>
    </Paper>
  );
};

export default ExamTimeoutCard;
