import React, { useMemo, useState } from "react";
import { makeStyles } from "@mui/styles"; // Import from @mui instead of @material-ui/core
import {
  Typography,
  Grid,
  Card,
  Box,
  Chip,
  Button,
  CardActionArea,
  Fade,
} from "@mui/material";
import { useNavigate } from "react-router-dom";
import HourglassTopIcon from "@mui/icons-material/HourglassTop";
import HourglassBottomIcon from "@mui/icons-material/HourglassBottom";
import classnames from "classnames";

const useStyles = makeStyles((theme) => ({
  root: {},
  card: {
    height: "100%",
    display: "flex",
    flexDirection: "column",
  },
  examTitleText: {
    wordBreak: "break-word",
  },
}));

const ExamCard = ({ exam }) => {
  const classes = useStyles();

  const navigate = useNavigate();

  const [buttonVisibility, setButtonVisibility] = useState(false);

  const handleRedirectToExams = () => {
    navigate(`/courses/exam/${exam.id}/details`);
  };

  const examStatus = useMemo(() => {
    switch (exam.status) {
      case "EXAM_NOT_TAKEN":
        return {
          title: "Pending",
          color: "primary",
        };
      case "EXAM_IS_READY":
      case "EXAM_IN_PROGRESS":
        return {
          title: "In Progress",
          color: "info",
        };
      case "EXAM_ALL_QUESTIONS_ANSWERED":
        return {
          title: "In Progress (almost done)",
          color: "info",
        };
      case "EXAM_EXPIRED":
        return {
          title: "Expired",
          color: "warning",
        };
      default:
        return {
          title: "In Progress",
          color: "info",
        };
    }
  }, [exam]);

  return (
    <Grid
      item
      xs={12}
      sm={4}
      md={3}
      onMouseEnter={() => {
        setButtonVisibility(true);
      }}
      onMouseLeave={() => {
        setButtonVisibility(false);
      }}
    >
      <Card
        sx={{
          display: "flex",
          flexWrap: "wrap",
          zIndex: 1,
        }}
        elevation={1}
      >
        <CardActionArea
          sx={{
            p: 2,
            display: "flex",
            flexWrap: "wrap",
            zIndex: 1,
          }}
          onClick={handleRedirectToExams}
        >
          <Box
            sx={{ alignSelf: "center" }}
            textAlign="left"
            display="flex"
            flexDirection="column"
            gap={2}
            flex={1}
          >
            <Box display="flex" flexDirection="row" gap={1}>
              {/* {exam.subjects.map(({ Group }) => (
                <Typography
                  variant="body2"
                  color="text.secondary"
                  fontWeight="bold"
                  className={classes.examTitleText}
                >
                  {Group.group_name}
                </Typography>
              ))} */}
            </Box>
            <Typography
              fontWeight="bold"
              noWrap={false}
              gutterBottom
              className={classes.examTitleText}
              color="primary"
            >
              {exam.title}
            </Typography>
            <Box>
              <Typography
                noWrap
                gutterBottom
                display="flex"
                variant="subtitle1"
                color="#ababab"
                fontSize={12}
              >
                <HourglassTopIcon fontSize="small" /> {exam.startDatetime}
              </Typography>
              <Typography
                noWrap
                gutterBottom
                display="flex"
                variant="subtitle1"
                color="#ababab"
                fontSize={12}
              >
                <HourglassBottomIcon fontSize="small" /> {exam.endDatetime}
              </Typography>
            </Box>
            <Box>
              <Chip
                size="small"
                variant="filled"
                label={examStatus.title}
                sx={(theme) => ({
                  ".MuiChip-icon": {
                    fontSize: 16,
                    ml: "4px",
                    color: "success.500",
                  },
                  bgcolor: "success.50",
                  borderColor: "success.100",
                  color: "success.900",
                })}
                color={examStatus.color}
              />
            </Box>

            <Fade in={buttonVisibility}>
              <Button
                variant="contained"
                color="primary"
                sx={{ textTransform: "none" }}
                className={classnames(classes.examButton, {})}
              >
                Take Exam
              </Button>
            </Fade>
          </Box>
        </CardActionArea>
      </Card>
    </Grid>
  );
};

export default ExamCard;
