const ExamFreeTextAnswer = ({
  options,
  question,
  examId,
  onOptionSelected,
  onAnswerWritten,
}) => {
  const classes = useStyles();

  const [text, setText] = useState("");

  const handleChange = (event) => {
    setText(event.target.value);
  };

  useEffect(() => {
    if (!text) return; // Prevent running effect when text is empty

    const typingTimeout = setTimeout(() => {
      if (onAnswerWritten) {
        onAnswerWritten(question.id, text);
      }
    }, 600); // Wait 2 seconds after last input

    return () => clearTimeout(typingTimeout); // Cleanup timeout on text change
  }, [text]);

  useEffect(() => {
    return () => {
      if (text.trim() && onAnswerWritten) {
        console.log("Saving answer before unmount:", text);
        onAnswerWritten(question.id, text);
      }
    };
  }, []);

  return (
    <Box>
      <TextField
        label="Write your answer here"
        placeholder="Write you Answer here"
        multiline
        variant="standard"
        fullWidth
        minRows={5}
        maxRows={25}
        value={text || question.answerText}
        onChange={handleChange}
      />
    </Box>
  );
};

export default ExamFreeTextAnswer;
