import "./App.css";
import Login from "./pages/login/Login";
import CoursesAndExams from "./pages/courses/CoursesAndExams";
import StudentExam from "./pages/tests/StudentExam";
import { createBrowserRouter, RouterProvider } from "react-router-dom";
import CourseDetails from "./pages/course-details/CourseDetails";
import StudentsLayout from "./layouts/StudentsLayout";
import RequireAuth from "./middleware/RequireAuth";
import ConvertPDFToTVC from "./pages/login/ConvertPDFToTVC";

const router = createBrowserRouter([
  {
    path: "/",
    element: <Login />,
  },
  {
    path: "/logout",
    element: (
      <RequireAuth>
        <Login />
      </RequireAuth>
    ),
  },
  {
    path: "/courses",
    element: (
      <RequireAuth>
        <StudentsLayout />
      </RequireAuth>
    ),
    children: [
      {
        path: "",
        element: <CoursesAndExams />,
      },
      {
        path: "exam/:examId/details",
        element: <CourseDetails />,
      },
      {
        path: "exam/:examId",
        element: <StudentExam />,
      },
    ],
  },
]);

function App() {
  return (
    <div className="App">
      <RouterProvider router={router} />
    </div>
  );
}

export default App;
