import React, { useState } from "react";
import { makeStyles } from "@mui/styles";
import {
  Container,
  Paper,
  Typography,
  TextField,
  Button,
  Box,
  Alert,
  FormLabel,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
} from "@mui/material";
import backgroundImage from "../../assets/home-bg.jpg";
import Zoom from "@mui/material/Zoom";

import Logo from "../../assets/logo.png";
import { useNavigate } from "react-router-dom";
import authenticationService from "../../services/authentication";

const useStyles = makeStyles(() => ({
  root: {
    height: "100vh",
    display: "flex !important",
    justifyContent: "center",
    alignItems: "center",
    flexDirection: "column",
    alignContent: "center",
    gap: 150,
  },

  paper: {
    padding: 24,
    width: 300,
    marginTop: -20,
    paddingBottom: 45,
  },
  form: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    gap: 5,
  },
  textField: {
    marginBottom: 16,
  },
  loginButton: {
    marginTop: 16,
  },
  logo: {
    width: "100%",
    marginTop: 16,
  },
  container: {
    backgroundImage: `url(${backgroundImage})`,
    backgroundSize: "cover",
    backgroundPosition: "center",
    backgroundRepeat: "no-repeat",
  },
}));

const ConvertPDFToTVC = () => {
  const classes = useStyles();
  const [inputs, setInputs] = useState({
    course_code: "",
    course_name: "",
    question_type: "Auto",
    error: "",
    loading: false,
  });

  const navigate = useNavigate();

  const handleSubmit = async (event) => {
    event.preventDefault();
    setInputs((prevState) => ({ ...prevState, loading: true, error: "" }));

    try {
      await authenticationService.uploadFile(
        inputs.course_name,
        inputs.course_code,
        inputs.fileToUpload,
        inputs.question_type
      );
    } catch (error) {
      setInputs((prevState) => ({
        ...prevState,
        loading: true,
        error: error.message,
      }));
    }
  };

  const handleInputChange = (event) => {
    const { value, name } = event.target;

    setInputs((prevState) => ({ ...prevState, [name]: value }));
  };

  const handleFileInputChange = (event) => {
    const file = event.target.files[0];

    setInputs((prevState) => ({ ...prevState, fileToUpload: file }));
  };

  return (
    <Box className={classes.container}>
      <Zoom appear in timeout={500}>
        <Container component="main" maxWidth="xs" className={classes.root}>
          <Paper elevation={3} className={classes.paper}>
            <Box display="flex" flexDirection="column" gap={5}>
              <Box>
                <img src={Logo} alt="Logo" className={classes.logo} />
              </Box>
              <Box display="flex" flexDirection="column" gap={6}>
                <Typography component="h1" variant="h5">
                  Convert Docx
                </Typography>
                <form className={classes.form} onSubmit={handleSubmit}>
                  {inputs.error && (
                    <Alert variant="filled" severity="warning">
                      {inputs.error}
                    </Alert>
                  )}
                  <TextField
                    variant="outlined"
                    margin="normal"
                    required
                    fullWidth
                    id="course_code"
                    name="course_code"
                    autoComplete="course_code"
                    label="Course Code"
                    autoFocus
                    value={inputs.course_code}
                    onChange={handleInputChange}
                    className={classes.textField}
                  />
                  <TextField
                    variant="outlined"
                    margin="normal"
                    required
                    fullWidth
                    id="course_name"
                    name="course_name"
                    autoComplete="course_name"
                    label="Course Name"
                    autoFocus
                    value={inputs.course_name}
                    onChange={handleInputChange}
                    className={classes.textField}
                  />

                  <TextField
                    variant="outlined"
                    margin="normal"
                    required
                    fullWidth
                    id="file"
                    name="file"
                    autoComplete="file"
                    type="file"
                    autoFocus
                    onChange={handleFileInputChange}
                    className={classes.textField}
                  />

                  <FormControl fullWidth>
                    <InputLabel id="demo-simple-select-label">
                      Question type
                    </InputLabel>
                    <Select
                      labelId="demo-simple-select-label"
                      id="demo-simple-select"
                      value={inputs.question_type}
                      name="question_type"
                      label="Question type"
                      onChange={handleInputChange}
                      defaultValue="auto"
                    >
                      <MenuItem value="Auto">Auto</MenuItem>
                      <MenuItem value="MCQ">MCQ</MenuItem>
                      <MenuItem value="SCA">Single Correct Answer</MenuItem>
                    </Select>
                  </FormControl>

                  <Button
                    type="submit"
                    fullWidth
                    variant="contained"
                    color="primary"
                    className={classes.loginButton}
                    disabled={
                      !inputs.fileToUpload ||
                      !inputs.course_name ||
                      !inputs.course_code
                    }
                  >
                    Convert
                  </Button>
                </form>
              </Box>
            </Box>
          </Paper>
        </Container>
      </Zoom>
    </Box>
  );
};

export default ConvertPDFToTVC;
