import React from "react";
import { makeStyles } from "@mui/styles";
import {
  Container,
  Typography,
  Grid,
  Box,
  Alert,
  CircularProgress,
} from "@mui/material";
import SchoolLogo from "../../assets/logo.png";
import PowerSettingsNewIcon from "@mui/icons-material/PowerSettingsNew";
import Fab from "@mui/material/Fab";
import ExamCard from "./ExamCard";
import examService from "../../services/exam";
import useExams from "../../hooks/useExams";

const useStyles = makeStyles((theme) => ({
  root: {},
  card: {
    height: "100%",
    display: "flex",
    flexDirection: "column",
  },
  cardContent: {
    flexGrow: 1,
  },
  appBar: {
    marginBottom: 42,
    paddingRight: 15,
    paddingLeft: 15,
  },
  toolbar: {
    display: "flex",
    justifyContent: "space-between",
  },
  avatar: {
    height: 48,
  },
  gridContainer: {
    maxWidth: 800,
    margin: "0 auto",
  },
  examTitleText: {
    wordBreak: "break-word",
  },
}));

const CoursesAndExams = () => {
  const classes = useStyles();
  const { exams, loading, error } = useExams();

  return (
    <Box display="flex" flexDirection="column" gap={5} mt={4}>
      <Typography variant="h4" gutterBottom>
        Available Exams/Tests
      </Typography>
      <Grid container spacing={3} justifyContent="center">
        {loading && (
          <Box
            display="flex"
            flexDirection="column"
            alignItems="center"
            justifyContent="center"
            py={5}
          >
            <CircularProgress size={40} />
            <Typography variant="h6" sx={{ mt: 2, color: "text.secondary" }}>
              Loading exams/tests...
            </Typography>
          </Box>
        )}

        {!loading &&
          exams.length > 0 &&
          exams.map((exam) => <ExamCard item key={exam.id} exam={exam} />)}

        {!loading && exams.length === 0 && (
          <Alert
            variant="filled"
            severity="warning"
            sx={{ width: "100%", maxWidth: 400, mx: "auto" }}
          >
            You are currently not registered for an exam. Please contact your
            instructor.
          </Alert>
        )}

        {!loading && error && (
          <Alert
            variant="filled"
            severity="error"
            sx={{ width: "100%", maxWidth: 400, mx: "auto" }}
          >
            Error loading exams: {error.message || "Something went wrong."}
          </Alert>
        )}
      </Grid>
    </Box>
  );
};

export default CoursesAndExams;
