import * as queueStorage from "./queueStorage";
import httpService from "./http";
import { useState, useEffect } from "react";

const PROCESS_INTERVAL = 5000;
let processingIntervalId = null;
let isProcessing = false;

const processNextQueueItem = async () => {
  if (isProcessing) {
    return;
  }

  isProcessing = true;

  const queueLength = await queueStorage.getQueueLength();
  if (queueLength === 0) {
    isProcessing = false;
    return;
  }

  const queueItem = await queueStorage.getOldestRequest();

  if (!queueItem || !queueItem.request) {
    isProcessing = false;
    return;
  }

  console.log("V::: ", queueItem);
  const {
    id: requestId,
    request: { url, httpMethod, body },
  } = queueItem;

  try {
    if (httpMethod === "post") {
      await httpService.post(url, body);
    } else if (httpMethod === "get") {
      await httpService.get(url);
    } else {
      await queueStorage.removeRequest(requestId);
      isProcessing = false;
      processNextQueueItem();
      return;
    }

    await queueStorage.removeRequest(requestId);
  } catch (error) {
    await new Promise((resolve) =>
      setTimeout(() => {
        resolve();
      }, 3000)
    );
  } finally {
    isProcessing = false;
    const remainingLength = await queueStorage.getQueueLength();
    if (remainingLength > 0) {
      processNextQueueItem();
    }
  }
};

export const startQueueProcessor = () => {
  if (processingIntervalId) {
    return;
  }

  processNextQueueItem();

  processingIntervalId = setInterval(processNextQueueItem, PROCESS_INTERVAL);
};

export const stopQueueProcessor = () => {
  if (processingIntervalId) {
    clearInterval(processingIntervalId);
    processingIntervalId = null;
    isProcessing = false;
    console.log("[QueueProcessor] Background processor stopped.");
  } else {
    console.log("[QueueProcessor] Processor not running.");
  }
};

export const useQueueLength = () => {
  const [length, setLength] = useState(0);

  useEffect(() => {
    const updateLength = async () => {
      const currentLength = await queueStorage.getQueueLength();
      setLength(currentLength);
    };

    updateLength();

    const interval = setInterval(updateLength, 1000);

    return () => clearInterval(interval);
  }, []);

  return length;
};
