import { useCallback, useEffect, useState } from "react";
import examService from "../services/exam";
import { redirect, useNavigate } from "react-router-dom";

export default function useExams() {
  const [exams, setExams] = useState([]);
  const [loading, setLoading] = useState(true);
  const [examState, setExamState] = useState({
    isStartingExam: false,
    loadedExamId: null,
    questions: [],
    currentQuestionIndex: null,
    loadTime: null,
    testEndTime: null,
    timeUp: false,
    testDurationInSeconds: null,
    isFinished: false,
    isFinishing: false,
    isNotLoaded: false,
    examError: null,
    message: null,
  });
  const navigate = useNavigate();

  useEffect(() => {
    (async () => {
      setLoading(true);
      const exams = await examService.fetchUserExams();

      setExams(exams);
      setLoading(false);
    })();
  }, []);

  useEffect(() => {
    if (examState.currentQuestionIndex !== null) {
      const question = examState.questions[examState.currentQuestionIndex];

      if (question) {
        markQuestionAsSeen(examState.loadedExamId, question.id);
      }
    }
  }, [examState.loadedExamId, examState.currentQuestionIndex]);

  const findByExamId = useCallback(
    (testId) => {
      return exams.find(({ id }) => id == testId);
    },
    [exams]
  );

  const updateExamState = (param) => {
    setExamState((prevState) => ({ ...prevState, ...param }));
  };

  const startExam = async (examId) => {
    updateExamState({
      isStartingExam: true,
      loadedExamId: null,
      currentQuestionIndex: null,
      examError: null,
    });
    try {
      const exam = await examService.startUserExam(examId);

      if (exam) {
        const {
          questions,
          endDatetime,
          testDurationInSeconds,
          isFinished,
          isNotLoaded,
          message,
        } = exam;

        updateExamState({
          questions,
          testEndTime: endDatetime,
          testDurationInSeconds,
          isFinished,
          isNotLoaded,
          message,
          examError: isNotLoaded ? "not-loaded" : isFinished ? "expired" : null,
        });
      } else {
        setTimeout(() => {
          startExam(examId);
        }, 4000);
        return;
      }

      setTimeout(() => {
        updateExamState({
          isStartingExam: false,
          loadedExamId: examId,
          loadTime: Date.now(),
          currentQuestionIndex: 0,
        });
      }, 5000);
    } catch (error) {
      setTimeout(() => {
        startExam(examId);
      }, 2000);
    }
  };

  const markQuestionAsSeen = async (examId, questionId) => {
    const question = examState.questions.find(({ id }) => id == questionId);

    if (question.seen) {
      return;
    }

    await examService.markQuestionAsSeen(examId, questionId);
    const updatedQuestions = examState.questions.map((question) => {
      if (question.id == questionId) {
        question.seen = true;
      }

      return question;
    });

    updateExamState({ questions: updatedQuestions });
  };

  const setCurrentQuestionIndex = (questionIndex) => {
    updateExamState({ currentQuestionIndex: questionIndex });
  };

  const hasSeenQuestion = (questionIndex) => {
    const question = examState.questions[questionIndex];

    return Boolean(question.seen);
  };

  const hasSeenAndAnsweredQuestion = (questionIndex) => {
    const question = examState.questions[questionIndex];

    if (Boolean(question.seen)) {
      const options = question.options;

      return Boolean(
        question.answerText || options.find(({ selected }) => selected != -1)
      );
    }

    return false;
  };

  const onOptionSelected = async (
    questionId,
    optionId,
    value,
    valueOfOthers
  ) => {
    let selectedOption = null;
    let testLogId = null;
    const lastReactionTime = examState.loadTime;

    const updatedQuestions = examState.questions.map((question) => {
      if (question.id === questionId) {
        question.seen = true;

        const updatedOptions = question.options.map((option) => {
          if (option.id === optionId) {
            option.selected = value;

            selectedOption = option;
          } else {
            if (valueOfOthers !== undefined) {
              option.selected = valueOfOthers;
            }
          }

          return option;
        });

        question.options = updatedOptions;
        testLogId = question.testLogId;
      }

      return question;
    });

    updateExamState({ questions: updatedQuestions, loadTime: Date.now() });

    if (selectedOption) {
      // update this on our backend
      await examService.submitAnAnswer(
        examState.loadedExamId,
        questionId,
        optionId,
        testLogId,
        selectedOption.ansTestLogId,
        Date.now() - lastReactionTime,
        value
      );
    }
  };

  const examTimeUp = () => {
    updateExamState({ timeUp: true });
    examService.userExamTimeUp(examState.loadedExamId);
  };

  const finishExam = async () => {
    try {
      updateExamState({ isFinishing: true });
      await examService.terminateUserExam(examState.loadedExamId);

      updateExamState({ isFinished: true, isFinishing: false });
      // navigate(`/courses/exam/${examState.loadedExamId}/details`);

      return true;
    } catch (error) {
      console.log("failed to finishExam::: ", error);
    }
  };

  const getCurrentExam = async (examId) => {
    return exams.find(({ test_id }) => test_id == examId);
  };

  const onAnswerWritten = async (questionId, answerText) => {
    let testLogId = null;
    const lastReactionTime = examState.loadTime;

    const updatedQuestions = examState.questions.map((question) => {
      if (question.id == questionId) {
        question.seen = true;

        question.answerText = answerText;
        testLogId = question.testLogId;
      }

      return question;
    });

    updateExamState({ questions: updatedQuestions, loadTime: Date.now() });

    // update this on our backend
    await examService.submitAnAnswer(
      examState.loadedExamId,
      questionId,
      null,
      testLogId,
      null,
      Date.now() - lastReactionTime,
      null,
      answerText
    );
  };

  return {
    exams,
    loading,
    findByExamId,
    examState,
    startExam,
    setCurrentQuestionIndex,
    hasSeenAndAnsweredQuestion,
    markQuestionAsSeen,
    hasSeenQuestion,
    onOptionSelected,
    examTimeUp,
    finishExam,
    getCurrentExam,
    onAnswerWritten,
  };
}
