import httpService from "./http";

const authenticationService = {};

authenticationService.loginUser = async (username, password) => {
  try {
    const { user, accessToken } = await httpService.post(
      "auth/login",
      {
        username,
        password,
      },
      true
    );

    authenticationService.storeCurrentAuthenticatedUser(accessToken, user);
  } catch (error) {
    throw error;
  }
};

authenticationService.uploadFile = async (
  course_name,
  course_code,
  file,
  question_type
) => {
  const formData = new FormData();

  formData.append("course_name", course_name);
  formData.append("course_code", course_code);
  formData.append("question_type", question_type);

  formData.append("file", file);

  try {
    const response = await fetch("http://localhost:5000/API/exams/convert", {
      method: "POST",
      body: formData,
    });

    if (!response.ok) {
      throw new Error("Failed to upload file");
    }

    const blob = await response.blob();

    const contentDisposition = response.headers.get("Content-Disposition");
    let filename = "downloaded_file.tsv";

    if (contentDisposition && contentDisposition.includes("filename=")) {
      filename = contentDisposition
        .split("filename=")[1]
        .replace(/"/g, "")
        .trim();
    }

    console.log("filename::: ", filename);

    const url = window.URL.createObjectURL(blob);
    const a = document.createElement("a");
    a.href = url;
    a.download = filename;
    document.body.appendChild(a);
    a.click();

    // a.remove();
    // window.URL.revokeObjectURL(url);
  } catch (error) {
    console.error("Error uploading file:", error);
    throw error;
  }
};

authenticationService.isAuthenticated = () => {
  return Boolean(sessionStorage.getItem("accessToken"));
};

authenticationService.storeCurrentAuthenticatedUser = (accessToken, user) => {
  sessionStorage.setItem("accessToken", accessToken);
  sessionStorage.setItem("user", JSON.stringify(user));
};

authenticationService.getUserAndToken = () => {
  const accessToken = sessionStorage.getItem("accessToken");

  if (!accessToken) {
    return {};
  }
  try {
    const user = JSON.parse(sessionStorage.getItem("user"));

    return { user, accessToken };
  } catch (error) {
    return {};
  }
};

authenticationService.destroyUserSession = () => {
  sessionStorage.removeItem("accessToken");
  sessionStorage.removeItem("user");
};

export default authenticationService;
